import isEmpty from 'validator/lib/isEmpty';
import isEmail from 'validator/lib/isEmail';
import isLength from 'validator/lib/isLength';
import strings from './localizedStrings';

const validators = {
  required: value =>
    !value || isEmpty(String(value)) ? strings.validation.required : undefined,
  email: value =>
    !value || !isEmail(value) ? strings.validation.email : undefined,
  minLength: requiredLength => value =>
    value && !isLength(value, { min: requiredLength })
      ? strings.formatString(strings.validation.minLength, requiredLength)
      : undefined,
  maxLength: requiredLength => value =>
    value && !isLength(value, { max: requiredLength })
      ? strings.formatString(strings.validation.maxLength, requiredLength)
      : undefined,
  password: value =>
    value &&
    !/^(?=.*\d)(?=.*[!@#$%^&*])(?=.*[a-z])(?=.*[a-z]).{1,}$/i.test(value)
      ? strings.validation.password
      : undefined
};

export default validators;
