import {
  API_ENDPOINT_USER_LOGIN,
  API_ENDPOINT_USER_REQUEST_PASSWORD,
  API_ENDPOINT_USER_VERIFY_PASSWORD,
  API_ENDPOINT_USER_RESET_PASSWORD,
  API_ENDPOINT_CHANGE_PASSWORD
} from './endpoints';

import request from './request';

function login(data) {
  return request({
    endpoint: API_ENDPOINT_USER_LOGIN,
    method: 'POST',
    noLoginRequired: true,
    data
  }).promise;
}

function forgotPassword(emailId) {
  return request({
    endpoint: {
      url: `${API_ENDPOINT_USER_REQUEST_PASSWORD}/${emailId}`
    },
    noLoginRequired: true,
    method: 'GET'
  }).promise;
}

function verifyToken(token) {
  return request({
    endpoint: {
      url: `${API_ENDPOINT_USER_VERIFY_PASSWORD}/${token}`
    },
    noLoginRequired: true,
    method: 'GET'
  }).promise;
}

function resetPassword(data) {
  return request({
    endpoint: {
      url: API_ENDPOINT_USER_RESET_PASSWORD
    },
    noLoginRequired: true,
    method: 'PUT',
    data
  }).promise;
}

function changePassword(data) {
  return request({
    endpoint: {
      url: API_ENDPOINT_CHANGE_PASSWORD
    },
    method: 'PUT',
    data
  }).promise;
}

export default {
  login,
  forgotPassword,
  verifyToken,
  resetPassword,
  changePassword
};
