import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Redirect, Route, Switch } from 'react-router-dom';
import ChangePassword from '../ChangePassword';
import { PrivateLayout } from '../Shared';
import {
  ROUTE_LOGIN,
  ROUTE_SNAP_BUILDER,
  ROUTE_BATCH_PROCESSING,
  ROUTE_DOWNLOAD_REPORTS,
  ROUTE_USER_LIST,
  ROUTES_TOP_TABS,
  ROUTE_CHANGEPASSWORD
} from '../../utils';
import { authSelectors } from '../../modules/auth';
import SnapBuilder from '../SnapBuilder';

function BatchProcessing() {
  return <h1>Batch Processing View</h1>;
}

function DownloadReport() {
  return <h1>Download Report View</h1>;
}

function UsersList() {
  return <h1>Users List View</h1>;
}

const components = {
  [ROUTE_SNAP_BUILDER]: SnapBuilder,
  [ROUTE_BATCH_PROCESSING]: BatchProcessing,
  [ROUTE_DOWNLOAD_REPORTS]: DownloadReport,
  [ROUTE_USER_LIST]: UsersList,
  [ROUTE_CHANGEPASSWORD]: ChangePassword
};

const propTypes = {
  isLoggedIn: PropTypes.bool.isRequired,
  userData: PropTypes.shape({
    role: PropTypes.string,
    getUserData: PropTypes.func
  }).isRequired
};

class SecureRoutes extends PureComponent {
  getFilteredRoutes() {
    const { userData } = this.props;

    return ROUTES_TOP_TABS.filter(route => route.roles.includes(userData.role));
  }

  render() {
    const { isLoggedIn } = this.props;

    if (!isLoggedIn) {
      return <Redirect to={ROUTE_LOGIN} />;
    }

    const filteredRoutes = this.getFilteredRoutes();

    return (
      <Switch>
        {filteredRoutes.map(secureRoute => (
          <Route
            key={secureRoute.url}
            path={secureRoute.url}
            render={route => (
              <PrivateLayout
                component={components[secureRoute.url]}
                route={route}
              />
            )}
          />
        ))}
        <Route
          path={ROUTE_CHANGEPASSWORD}
          render={route => (
            <PrivateLayout component={ChangePassword} route={route} />
          )}
        />

        <Redirect to={filteredRoutes[0].url} />
      </Switch>
    );
  }
}

SecureRoutes.propTypes = propTypes;

const mapStateToProps = state => ({
  isLoggedIn: authSelectors.isLoggedIn(state),
  userData: authSelectors.getUserData(state)
});

export default connect(mapStateToProps)(SecureRoutes);
