import types from './authTypes';

function loginSuccess(payload) {
  return { type: types.LOGIN_SUCCESS, payload };
}

function logoutSuccess(payload) {
  return { type: types.LOGOUT_SUCCESS, payload };
}

function forgotPasswordSuccess(payload) {
  return { type: types.REQUEST_PASSWORD_SUCCESS, payload };
}

function changePasswordRequest(payload) {
  return { type: types.CHANGE_PASSWORD_REQUEST, payload };
}

function setVerifyToken(payload) {
  return { type: types.SET_VERIFY_TOKEN, payload };
}
export default {
  loginSuccess,
  logoutSuccess,
  forgotPasswordSuccess,
  setVerifyToken,
  changePasswordRequest
};
