import React, { PureComponent } from 'react';
import { Redirect } from 'react-router-dom';
import { Container } from 'react-bootstrap';
import PropTypes from 'prop-types';
import { ROUTE_LOGIN } from '../../../utils';
import Header from './Header';
import Footer from './Footer/Footer';
import './PrivateLayout.scss';

const propTypes = {
  component: PropTypes.elementType.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  route: PropTypes.shape({
    location: PropTypes.object
  }).isRequired
};

class PrivateLayout extends PureComponent {
  render() {
    const { component: Component, isLoggedIn, route } = this.props;

    return !isLoggedIn ? (
      <Redirect to={ROUTE_LOGIN} />
    ) : (
      <div className="private-layout d-flex flex-column">
        {/* passing location to header so that it will re-render when
          location changes */}
        <Header location={route.location} />

        <Container className="flex-grow-1">
          <Component {...route} />
        </Container>

        <Footer />
      </div>
    );
  }
}
PrivateLayout.propTypes = propTypes;

export default PrivateLayout;
