import React, { Component } from 'react';
import { Row, Col, Form, Button, Breadcrumb } from 'react-bootstrap';
import PropTypes from 'prop-types';
import { Field, reduxForm } from 'redux-form';
import FieldMultiSelect from '../../Shared/FieldMultiSelect';
import CustomRadio from '../../Shared/CustomRadio';
import imageIcon from '../../../assets/image.svg';
import videoIcon from '../../../assets/Video.svg';
import errorIcon from '../../../assets/error_msg.svg';
import { CustomCheckbox, FieldInput } from '../../Shared';
import './UploadCreatives.scss';
import {
  validators,
  FORM_NAME_UPLOAD_CREATIVES,
  ROUTE_SNAP_BUILDER
} from '../../../utils';
import { listItemType } from './uploadCreativesType';

const rejectedFileType = PropTypes.shape({
  id: PropTypes.number
});
const propTypes = {
  change: PropTypes.func.isRequired,
  list: PropTypes.arrayOf(listItemType).isRequired,
  dirty: PropTypes.bool.isRequired,
  rejectedFile: PropTypes.arrayOf(rejectedFileType).isRequired,
  fileCount: PropTypes.number.isRequired,
  onFileChange: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired
};

class UploadCreatives extends Component {
  state = {
    isAllAccountSelected: false
  };

  onClickSelectAll(v) {
    const { change, list } = this.props;
    if (v === true) {
      change('adAccount', list);
      this.setState({
        isAllAccountSelected: true
      });
    } else {
      this.setState({
        isAllAccountSelected: false
      });
    }
  }

  render() {
    const {
      dirty,
      rejectedFile,
      fileCount,
      onFileChange,
      handleSubmit,
      list
    } = this.props;
    const { isAllAccountSelected } = this.state;

    const rejectedFileName = rejectedFile.map(file => file.name);

    return (
      <div className="uploadCreatives-form">
        <Breadcrumb>
          <Breadcrumb.Item href={ROUTE_SNAP_BUILDER}>
            Snap Builder
          </Breadcrumb.Item>

          <Breadcrumb.Item active>Upload Creatives</Breadcrumb.Item>
        </Breadcrumb>

        <h2>Upload Creatives</h2>
        <hr />
        <div className="uploadCreatives-form__label">Select Ad Type</div>

        <Form onSubmit={handleSubmit}>
          <div className="uploadCreatives-form__radio">
            <Field
              name="mediaView"
              type="radio"
              component={CustomRadio}
              validate={validators.required}
              inline
              options={[
                { title: 'Snap', id: 'Snap', value: 'Snap' },
                { title: 'Thumbnails', id: 'Thumbnails', value: 'Thumbnails' },
                { title: 'Logo', id: 'Logo', value: 'Logo' }
              ]}
            />
          </div>

          <div>
            <p>
              <span className="uploadCreatives-form__label">
                Select files to upload
              </span>
              <br />
              <span className="uploadCreatives-form__text">
                You can upload multiple creatives at time in Video and Image
                format.
              </span>
            </p>
          </div>

          <Row className="uploadCreatives-form__text">
            <Col xs={5}>
              <img src={videoIcon} alt="icon" />
              <span className="__semiBold">Video</span>
              <p>
                Upto 32mb/file
                <br />
                Accepted formats:
                <span className="__bold"> .mp4 </span>
                or
                <span className="__bold">.mov</span>
                and
                <span className="__bold">H.264</span>
                encoded
              </p>
            </Col>
            <Col className="__imageInfo">
              <img src={imageIcon} alt="imageicon" />
              <span className="__semiBold">Image</span>
              <p>
                Upto 5mb/file
                <br />
                Accepted formats:
                <span className="__bold"> .jpg</span>
                and
                <span className="__bold">.png</span>
              </p>
            </Col>
          </Row>

          <div className="uploadCreatives-form__upload">
            <Form.Control
              className="d-none"
              id="media-upload"
              multiple
              name="upfile"
              onChange={onFileChange}
              type="file"
              accept=".mp4,.mov,H.264,.jpg,.png"
            />

            <Form.Label className="btn btn-pri" htmlFor="media-upload">
              CHOOSE FILES
            </Form.Label>

            {fileCount !== 0 && (
              <span className="mt-2">
                {` ${fileCount}
               Files`}
              </span>
            )}

            <Field
              component={FieldInput}
              name="hiddenField"
              type="hidden"
              validate={validators.required}
            />

            {rejectedFile.length ? (
              <React.Fragment>
                <div className="text-danger">
                  <img src={errorIcon} alt="imageicon" />
                  {`Cannot upload ${rejectedFileName.map(
                    file => file
                  )} due to exceeding file size limit `}
                  <br />
                  <span className="ml-4 text-secondary">
                    Try uploading files within the max size limit.
                  </span>
                </div>
              </React.Fragment>
            ) : null}
          </div>

          <div className="uploadCreatives-form__linkText">
            <span className="uploadCreatives-form__label">
              Link to Ad Accounts
            </span>
            <br />
            You can link creatives to multiple Ad Accounts
          </div>

          <Row>
            <Col xs={5}>
              <Field
                name="adAccount"
                disabled={isAllAccountSelected}
                component={FieldMultiSelect}
                placeholder="Select Ad Accounts"
                valueField="id"
                textField="name"
                data={list}
                horizontal
                validate={validators.required}
              />
            </Col>
          </Row>

          <Field
            component={CustomCheckbox}
            label="Select all Ad accounts"
            name="allValues"
            type="checkbox"
            id="selectall"
            value="selectall"
            className="mr-4"
            onChange={(e, v) => this.onClickSelectAll(v)}
          />

          <Button
            disabled={rejectedFile.length || !dirty}
            type="submit"
            className="btn btn-secondary shadow rounded uploadCreatives-form__btn"
          >
            UPLOAD CREATIVES
          </Button>
        </Form>
      </div>
    );
  }
}
UploadCreatives.propTypes = propTypes;

export default reduxForm({
  form: FORM_NAME_UPLOAD_CREATIVES
})(UploadCreatives);
