import { combineReducers } from 'redux';
import types from './authTypes';

function loginDataReducer(state = null, action) {
  switch (action.type) {
    case types.LOGIN_SUCCESS:
      return { ...action.payload };

    default:
      return state;
  }
}

function forgotPasswordReducer(state = {}, action) {
  switch (action.type) {
    case types.REQUEST_PASSWORD_SUCCESS:
      return true;

    default:
      return state;
  }
}

function verifyPassword(state = {}, action) {
  switch (action.type) {
    case types.SET_VERIFY_TOKEN:
      return { ...state, data: action.payload.data };

    default:
      return state;
  }
}

function changePasswordReducer(state = {}, action) {
  switch (action.type) {
    case types.CHANGE_PASSWORD_REQUEST:
      return true;
    default:
      return state;
  }
}

const reducer = combineReducers({
  loginData: loginDataReducer,
  forgotPassword: forgotPasswordReducer,
  verifyPassword,
  changePassword: changePasswordReducer
});

export default reducer;
