import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { userDataStore } from '../../../../utils';
import { authSelectors, authOperations } from '../../../../modules/auth';
import Header from './Header';

const propTypes = {
  logoutRequest: PropTypes.func.isRequired
};

function HeaderContainer({ logoutRequest, ...rest }) {
  return (
    <Header
      logout={() => logoutRequest(() => userDataStore.unset())}
      {...rest}
    />
  );
}

const mapStateToProps = state => ({
  userData: authSelectors.getUserData(state)
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      logoutRequest: authOperations.logout
    },
    dispatch
  );

HeaderContainer.propTypes = propTypes;

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(HeaderContainer);
