import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import {
  ROUTE_LOGIN,
  ROUTE_FORGOTPASSWORD,
  ROUTE_RESETPASSWORD,
  ROUTE_SECURE_ROUTES
} from '../../utils';
import { AuthLayout } from '../Shared';
import SecureRoutes from './SecureRoutes';
import Login from '../Login';
import ForgotPassword from '../ForgotPassword';
import ResetPassword from '../ResetPassword';

function AppRoutes() {
  return (
    <Switch>
      <Route
        path={ROUTE_LOGIN}
        render={route => <AuthLayout component={Login} route={route} />}
      />

      <Route
        path={ROUTE_FORGOTPASSWORD}
        render={route => (
          <AuthLayout component={ForgotPassword} route={route} />
        )}
      />

      <Route
        path={ROUTE_RESETPASSWORD}
        render={route => <AuthLayout component={ResetPassword} route={route} />}
      />

      <Route path={ROUTE_SECURE_ROUTES} component={SecureRoutes} />

      <Redirect to={ROUTE_LOGIN} />
    </Switch>
  );
}

export default AppRoutes;
