import {
  API_ENDPOINT_SNAP_BUILDER_AD_ACCOUNT,
  API_ENDPOINT_SNAP_BUILDER_MEDIA_UPLOAD
} from './endpoints';
import request from './request';

function getAdAccountList() {
  return request({
    endpoint: API_ENDPOINT_SNAP_BUILDER_AD_ACCOUNT,
    method: 'GET'
  }).promise;
}

function uploadMedia(data) {
  return request({
    endpoint: API_ENDPOINT_SNAP_BUILDER_MEDIA_UPLOAD,
    method: 'POST',
    data
  }).promise;
}

export default {
  getAdAccountList,
  uploadMedia
};
