import axios from 'axios';
import { cancellablePromise, subParams } from '../utils';
import store from '../store';

function convertToAxiosOptions(options) {
  const { endpoint, ...rest } = options;
  const newOptions = { ...rest };

  if (!newOptions.url && endpoint) {
    newOptions.url =
      process.env.REACT_APP_API_BASE +
      (typeof endpoint === 'string'
        ? endpoint
        : subParams(endpoint.url, endpoint.params));
  }

  return newOptions;
}

const subscribeToStore = reqObj => {
  return store.subscribe(() => {
    if (!store.getState().auth.loginData) {
      reqObj.cancel();
    }
  });
};

function request(options) {
  const { noLoginRequired, ...rest } = options;
  const reqObj = cancellablePromise(axios.request(convertToAxiosOptions(rest)));

  if (!noLoginRequired) {
    const unsubscribeToStore = subscribeToStore(reqObj);
    reqObj.promise.then(() => unsubscribeToStore(), () => unsubscribeToStore());
  }

  return reqObj;
}

export default request;
