import PropTypes from 'prop-types';

const uploadStatusType = PropTypes.shape({
  adAccountId: PropTypes.string.isRequired
});

export const listItemType = PropTypes.shape({
  id: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired
});

export const mediaDataType = PropTypes.shape({
  uploadStatus: PropTypes.arrayOf(uploadStatusType).isRequired
});
