import React from 'react';
import { Route, Switch } from 'react-router-dom';
import {
  ROUTE_SNAP_BUILDER,
  ROUTES_SNAP_BUILDER_UPLOAD_CREATIVES
} from '../../utils';
import SnapBuilder from './SnapBuilderContainer';
import UploadCreatives from './UploadCreatives';

function SnapBuilderRoute() {
  return (
    <Switch>
      <Route exact path={ROUTE_SNAP_BUILDER} component={SnapBuilder} />

      <Route
        exact
        path={ROUTES_SNAP_BUILDER_UPLOAD_CREATIVES}
        component={UploadCreatives}
      />
    </Switch>
  );
}

export default SnapBuilderRoute;
