import React from 'react';
import { Link } from 'react-router-dom';

function ForgetPasswordSuccess() {
  return (
    <div className="formWrapper commonForAll">
      <div className="formBody">
        <h1 style={{ marginBottom: 25 }}>
          <b>Caleb</b>
        </h1>
        <p>
          <Link to="/">{'< Go back to Sign In page'}</Link>
        </p>

        <h5>
          <b> You have succussfully change your password</b>
        </h5>
        <p style={{ marginBottom: 25 }}>Please login with new password</p>
      </div>
    </div>
  );
}

export default ForgetPasswordSuccess;
