import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { change, isDirty, isValid } from 'redux-form';
import UploadCreatives from './UploadCreatives';
import UploadCreativesSuccess from './UploadCreativesSuccess';
import { snapBuilderOperations } from '../../../modules/snapBuilder';
import { FORM_NAME_UPLOAD_CREATIVES } from '../../../utils';
import '../Spinner.scss';
import { listItemType, mediaDataType } from './uploadCreativesType';

const propTypes = {
  getAdAccountList: PropTypes.func.isRequired,
  upload: PropTypes.func.isRequired,
  changeField: PropTypes.func.isRequired,
  list: PropTypes.arrayOf(listItemType).isRequired,
  mediaData: PropTypes.objectOf(mediaDataType).isRequired
};

class UploadCreativesContainer extends Component {
  state = {
    files: [],
    validFile: [],
    rejectedFile: [],
    isLoading: false,
    onSuccess: false
  };

  componentDidMount() {
    const { getAdAccountList } = this.props;
    getAdAccountList();
  }

  onSubmit(formData) {
    const { files } = this.state;
    const { upload } = this.props;

    this.setState({
      isLoading: true
    });

    const uploadData = new FormData();
    Array.from(files).forEach(file => uploadData.append(`upfile[]`, file));

    uploadData.append('mediaView', formData.mediaView);
    uploadData.append('adAccount', JSON.stringify(formData.adAccount));

    upload(
      uploadData,
      () => {
        this.setState({
          isLoading: false,
          onSuccess: true
        });
      },
      () => {
        this.setState({
          isLoading: false
        });
      }
    );
  }

  onFileChange = e => {
    const { changeField } = this.props;

    const temp1 = [];
    const temp2 = [];

    changeField('hiddenField', e.target.files.length === 0 ? null : 'data');

    Array.from(e.target.files).forEach(file => {
      const fileSize = file.size / 1000000;

      const fileType =
        file.type === 'video/mp4' || file.type === 'video/quicktime';

      if ((fileSize < 5 && !fileType) || (fileSize < 32 && fileType)) {
        temp1.push(file);
      } else {
        temp2.push(file);
      }
    });

    this.setState({
      files: [...Array.from(e.target.files)],
      validFile: [...temp1],
      rejectedFile: [...temp2]
    });
  };

  onClickUploadMoreCreatives() {
    this.setState({
      onSuccess: false
    });
  }

  render() {
    const { files, isLoading, onSuccess, validFile, rejectedFile } = this.state;
    const { list, mediaData } = this.props;
    return list ? (
      <React.Fragment>
        <div>
          {isLoading && (
            <div className="Spinner fixed">
              <div className="spinner-border text-dark inner" role="status" />
            </div>
          )}

          {onSuccess === false ? (
            <UploadCreatives
              list={list}
              fileCount={files.length}
              validFile={validFile}
              rejectedFile={rejectedFile}
              onFileChange={e => this.onFileChange(e)}
              onSubmit={data => this.onSubmit(data)}
            />
          ) : (
            <UploadCreativesSuccess
              fileCount={files.length}
              data={mediaData}
              onClickUploadMoreCreatives={() =>
                this.onClickUploadMoreCreatives()
              }
            />
          )}
        </div>
      </React.Fragment>
    ) : null;
  }
}

UploadCreativesContainer.propTypes = propTypes;

const mapStateToProps = state => ({
  list: state.snapBuilder.data,
  mediaData: state.snapBuilder.mediaData
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      getAdAccountList: snapBuilderOperations.getAdAccountList,
      upload: snapBuilderOperations.uploadMedia,
      changeField: (field, value) =>
        change(FORM_NAME_UPLOAD_CREATIVES, field, value),
      dirty: isDirty(FORM_NAME_UPLOAD_CREATIVES),
      valid: isValid(FORM_NAME_UPLOAD_CREATIVES)
    },
    dispatch
  );

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(UploadCreativesContainer);
