import { combineReducers } from 'redux';
import types from './snapBuilderTypes';

function adAccountReducer(state = [], action) {
  switch (action.type) {
    case types.SET_AD_ACCOUNT_LIST:
      return [...action.payload];

    default:
      return state;
  }
}

function uploadMedia(state = {}, action) {
  switch (action.type) {
    case types.SET_UPLOAD_MEDIA:
      return { ...action.payload };

    default:
      return state;
  }
}

const reducer = combineReducers({
  data: adAccountReducer,
  mediaData: uploadMedia
});

export default reducer;
