const USER_DATA_STORE_KEY = 'userData';

function getUserData() {
  const userData = sessionStorage.getItem(USER_DATA_STORE_KEY);
  return userData ? JSON.parse(userData) : null;
}

function setUserData(userData) {
  sessionStorage.setItem(USER_DATA_STORE_KEY, JSON.stringify(userData));
}

function unsetUserData() {
  sessionStorage.removeItem(USER_DATA_STORE_KEY);
}

export default {
  get: getUserData,
  set: setUserData,
  unset: unsetUserData
};
