import actions from './authActions';
import authService from '../../api/authService';

const loginRequest = (payload, callbackSuccess, callbackError) => dispatch => {
  authService.login(payload).then(
    ({ data }) => {
      callbackSuccess(data.data);
      dispatch(actions.loginSuccess(data.data));
    },
    error => {
      callbackError(error);
    }
  );
};

const logout = callbackSuccess => dispatch => {
  dispatch(actions.logoutSuccess());
  if (callbackSuccess) {
    callbackSuccess();
  }
};

const forgotPassword = (
  payload,
  callbackSuccess,
  callbackError
) => dispatch => {
  authService.forgotPassword(payload.email).then(
    ({ data }) => {
      dispatch(actions.forgotPasswordSuccess(data));
      callbackSuccess();
    },
    error => {
      callbackError(error);
    }
  );
};

const verifyToken = (token, callbackSuccess, callbackError) => dispatch => {
  authService.verifyToken(token).then(
    ({ data }) => {
      dispatch(actions.setVerifyToken(data));
      callbackSuccess(data);
    },
    error => {
      callbackError(error);
    }
  );
};

const resetPassword = (payload, callbackSuccess, callbackError) => () => {
  authService.resetPassword(payload).then(
    ({ data }) => {
      callbackSuccess(data);
    },
    error => {
      callbackError(error);
    }
  );
};

const changePassword = (payload, callbackSuccess, callbackError) => () => {
  authService.changePassword(payload).then(
    ({ data }) => {
      callbackSuccess(data);
    },
    error => {
      callbackError(error);
    }
  );
};

export default {
  loginRequest,
  logout,
  forgotPassword,
  verifyToken,
  resetPassword,
  changePassword
};
