import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { Alert } from 'react-bootstrap';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { authOperations } from '../../modules/auth';
import Login from './LoginPage';
import { userDataStore } from '../../utils';

const propTypes = {
  loginRequest: PropTypes.func.isRequired
};

class LoginContainer extends PureComponent {
  state = {
    errMsg: ''
  };

  onSubmit(formData) {
    const { loginRequest } = this.props;

    loginRequest(
      formData,
      data => {
        userDataStore.set(data);
      },
      error => {
        this.setState({
          errMsg: error.data.message
        });
      }
    );
  }

  render() {
    const { errMsg } = this.state;

    return (
      <React.Fragment>
        {errMsg ? (
          <Alert variant="danger" className="notPasswordScreen">
            {errMsg}
          </Alert>
        ) : null}
        <Login onSubmit={data => this.onSubmit(data)} {...this.props} />
      </React.Fragment>
    );
  }
}

LoginContainer.propTypes = propTypes;

const mapStateToProps = state => ({
  loginData: state.auth
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      loginRequest: authOperations.loginRequest
    },
    dispatch
  );

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LoginContainer);
