import React from 'react';
import PropTypes from 'prop-types';
import { Col, Form } from 'react-bootstrap';

const propTypes = {
  label: PropTypes.string.isRequired,
  horizontal: PropTypes.bool.isRequired,
  icon: PropTypes.symbol.isRequired,
  labelSize: PropTypes.string.isRequired,
  input: PropTypes.string.isRequired,
  labelProps: PropTypes.node.isRequired,
  groupProps: PropTypes.node.isRequired,
  meta: PropTypes.shape({
    error: PropTypes.string,
    warning: PropTypes.string,
    touched: PropTypes.bool
  }).isRequired
};

function FieldInput({
  horizontal,
  input,
  groupProps,
  label,
  labelProps,
  icon,
  meta: { error, warning, touched },
  ...props
}) {
  labelProps = horizontal ? { column: true, ...labelProps } : {};
  const formLabel = label ? (
    <Form.Label {...labelProps}>
      {icon && <i className={`mr-1 icon-${icon}`} />}
      {label}
    </Form.Label>
  ) : (
    ''
  );

  const control = (
    <React.Fragment>
      <Form.Control
        isInvalid={touched && (!!error || !!warning)}
        {...input}
        {...props}
      />

      {touched && (error || warning) && (
        <Form.Control.Feedback type="invalid">
          {error || warning}
        </Form.Control.Feedback>
      )}
    </React.Fragment>
  );

  return horizontal ? (
    <Form.Group as={Form.Row} {...groupProps}>
      {formLabel}
      <Col>{control}</Col>
    </Form.Group>
  ) : (
    <Form.Group {...groupProps}>
      {formLabel}
      {control}
    </Form.Group>
  );
}
FieldInput.propTypes = propTypes;

export default FieldInput;
