import React from 'react';

function CustomCheckbox({
  className,
  id,
  inline,
  input,
  isIndeterminate,
  label,
  labelProps,
  meta: { error, warning, touched } = {},
  ...props
}) {
  const setCheckboxRef = checkbox => {
    if (checkbox) {
      checkbox.indeterminate = isIndeterminate;
    }
  };

  const addClasses = [];

  const isInvalid = !!(touched && (error || warning));

  if (isInvalid) {
    addClasses.push('is-invalid');
  }

  if (inline) {
    addClasses.push('custom-control-inline align-middle');
  }

  const checkbox = (
    <div
      className={`custom-control custom-${props.type} mr-0 ${addClasses.join(
        ' '
      )} ${className}`}
    >
      <input
        className="custom-control-input"
        id={id}
        ref={setCheckboxRef}
        {...input}
        {...props}
      />
      <label className="custom-control-label" htmlFor={id} {...labelProps}>
        {label}
      </label>
    </div>
  );
  return isInvalid ? checkbox : checkbox;
}

export default CustomCheckbox;
