import React from 'react';
import { reduxForm } from 'redux-form';
import PropTypes from 'prop-types';
import { Row, Button, Breadcrumb } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
import { ROUTE_SNAP_BUILDER } from '../../../utils';
import errorIcon from '../../../assets/error_msg.svg';
import { mediaDataType } from './uploadCreativesType';
import './UploadCreatives.scss';

const propTypes = {
  onClickUploadMoreCreatives: PropTypes.func.isRequired,
  data: PropTypes.objectOf(mediaDataType).isRequired
};

function UploadCreativesSuccess(props) {
  const { data, onClickUploadMoreCreatives } = props;

  const uploadFailed = data.uploadStatus.filter(
    item => item.uploadFails.length > 0
  );

  return (
    <div className="uploadCreatives-form">
      <Breadcrumb>
        <Breadcrumb.Item href={ROUTE_SNAP_BUILDER}>
          Snap Builder
        </Breadcrumb.Item>

        <Breadcrumb.Item active>Upload Creatives</Breadcrumb.Item>
      </Breadcrumb>

      <h3>Upload Creatives</h3>
      <hr />

      {uploadFailed.length > 0 ? (
        <p>
          <img src={errorIcon} alt="imageicon" />
          <b className="text-danger">Could not upload some creatives.</b>
        </p>
      ) : (
        <p>
          <b className="text-success">Successfully uploaded Creatives</b>
        </p>
      )}

      <Row>
        <Button
          className="btn btn-secondary mr-2 float-right shadow rounded"
          onClick={onClickUploadMoreCreatives}
        >
          Upload More Creatives
        </Button>
        <NavLink
          className="btn btn-secondary mr-2 float-right shadow rounded"
          exact
          to={ROUTE_SNAP_BUILDER}
        >
          Go to SnapBuilder
        </NavLink>
      </Row>
    </div>
  );
}

UploadCreativesSuccess.propTypes = propTypes;

export default reduxForm({
  form: 'Upload_Creatives_Success_Form'
})(UploadCreativesSuccess);
