import React from 'react';
import PropTypes from 'prop-types';
import { Field, reduxForm } from 'redux-form';
import { Link } from 'react-router-dom';
import { Button, Form } from 'react-bootstrap';
import { ROUTE_FORGOTPASSWORD, validators } from '../../utils';
import { FieldInput } from '../Shared';
import './Login.scss';

const minLength8 = validators.minLength(8);

const propTypes = {
  handleSubmit: PropTypes.func.isRequired
};

function LoginPage({ handleSubmit }) {
  return (
    <div className="login-form">
      <div className="auth-layout-title">Sign in to your account</div>

      <Form
        autoComplete="off"
        className="auth-layout-form"
        onSubmit={handleSubmit}
      >
        <Field
          component={FieldInput}
          label="EMAIL"
          maxLength={50}
          name="email"
          placeholder="Enter Your Email"
          type="text"
          validate={[validators.required, validators.email]}
        />

        <Field
          component={FieldInput}
          label="PASSWORD"
          name="password"
          placeholder="******"
          type="password"
          validate={[validators.required, validators.password, minLength8]}
        />

        <div className="login-form__forgot-link">
          <Link to={ROUTE_FORGOTPASSWORD}>Forgot Password?</Link>
        </div>

        <Button
          variant="primary"
          className="btn-block auth-layout-submit-button"
          type="submit"
        >
          SIGN IN
        </Button>
      </Form>
    </div>
  );
}

LoginPage.propTypes = propTypes;

export default reduxForm({
  form: 'Login_Form'
})(LoginPage);
