import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { Alert } from 'react-bootstrap';
import { connect } from 'react-redux';
import { authOperations } from '../../modules/auth';
import ChangePassword from './ChangePassword';
import ChangePasswordSuccess from './ChangePasswordSuccess';

const propTypes = {
  changePassword: PropTypes.func.isRequired,
  onSubmitSuccess: PropTypes.func.isRequired
};

class ChangePasswordContainer extends Component {
  state = {
    passwordChanged: false,
    errMsg: ''
  };

  onSubmit(data) {
    const { changePassword, onSubmitSuccess } = this.props;

    changePassword(
      data,
      res => {
        onSubmitSuccess(res);
        this.setState({
          passwordChanged: true,
          errMsg: ''
        });
      },
      error => {
        this.setState({
          errMsg: error.data.message
        });
      }
    );
  }

  render() {
    const { errMsg, passwordChanged } = this.state;

    return (
      <React.Fragment>
        {errMsg ? (
          <Alert variant="danger" className="notPasswordScreen">
            {errMsg}
          </Alert>
        ) : null}

        {passwordChanged === false ? (
          <ChangePassword onSubmit={data => this.onSubmit(data)} />
        ) : (
          <ChangePasswordSuccess />
        )}
      </React.Fragment>
    );
  }
}

// const mapStateToProps = state => ({
//     changePassword: state.auth.changePassword
//   });

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      changePassword: authOperations.changePassword
    },
    dispatch
  );

ChangePasswordContainer.propTypes = propTypes;

export default connect(
  null,
  mapDispatchToProps
)(ChangePasswordContainer);
