import React from 'react';
import PropTypes from 'prop-types';
import { Field, reduxForm } from 'redux-form';
import { Button, Form } from 'react-bootstrap';
import FieldInput from '../Shared/FieldInput';
import Validators from '../../utils/validators';

const propTypes = {
  handleSubmit: PropTypes.func.isRequired
};

function ForgotPassword({ handleSubmit }) {
  return (
    <div className="forgetPassword-form">
      <div className="auth-layout-title">Password Reset</div>
      <div>
        To reset your password, enter the email address you use to sign in
      </div>

      <Form
        autoComplete="off"
        className="auth-layout-form"
        onSubmit={handleSubmit}
      >
        <Field
          component={FieldInput}
          label="Email"
          maxLength={50}
          name="email"
          placeholder="Enter Your Email"
          type="text"
          validate={[Validators.required, Validators.email]}
        />

        <Button
          className="btn-block auth-layout-submit-button"
          variant="primary"
          type="submit"
        >
          GET RESET LINK
        </Button>
      </Form>
    </div>
  );
}

ForgotPassword.propTypes = propTypes;

export default reduxForm({
  form: 'Forget_Password_Form'
})(ForgotPassword);
