import React from 'react';
import PropTypes from 'prop-types';
import { Field, reduxForm } from 'redux-form';
import { Button, Form } from 'react-bootstrap';
import FieldInput from '../Shared/FieldInput';
import Validators from '../../utils/validators';

const validatePassword = (value, allValues) =>
  value && value !== allValues.password
    ? 'Password and Confirm Password should match'
    : undefined;

const propTypes = {
  handleSubmit: PropTypes.func.isRequired
};

function ResetPassword({ handleSubmit }) {
  return (
    <div className="resetPassword-form">
      <div className="auth-layout-title">
        Please set a new password to sign in.
      </div>

      <div>
        <Form
          autoComplete="off"
          className="auth-layout-form"
          onSubmit={handleSubmit}
        >
          <Field
            component={FieldInput}
            label="Password"
            maxLength={50}
            name="password"
            placeholder="******"
            type="password"
            validate={[Validators.required, Validators.password]}
          />

          <Field
            component={FieldInput}
            label="Confirm Password"
            maxLength={50}
            name="confirmPassword"
            placeholder="******"
            type="password"
            validate={[
              Validators.required,
              Validators.password,
              validatePassword
            ]}
          />

          <Button
            className="btn-block auth-layout-submit-button"
            variant="primary"
            type="submit"
          >
            Set Password
          </Button>
        </Form>
      </div>
    </div>
  );
}

ResetPassword.propTypes = propTypes;

export default reduxForm({
  form: 'Forget_Password_Form'
})(ResetPassword);
