import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { Alert } from 'react-bootstrap';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { authOperations } from '../../modules/auth';
import ForgetPassword from './ForgotPassword';
import ForgetPasswordSuccess from './ForgetPasswordSuccess';

const propTypes = {
  forgotPassword: PropTypes.func.isRequired
};

class ForgetPasswordContainer extends PureComponent {
  state = {
    emailSend: false,
    errorMessage: ''
  };

  onSubmit(data) {
    const { forgotPassword } = this.props;
    forgotPassword(
      data,
      () => {
        this.setState({
          emailSend: true,
          errorMessage: ''
        });
      },
      error => {
        this.setState({ errorMessage: error.data.message });
      }
    );
  }

  render() {
    const { errorMessage, emailSend } = this.state;
    return (
      <React.Fragment>
        {errorMessage ? (
          <Alert variant="danger" className="notPasswordScreen">
            {errorMessage}
          </Alert>
        ) : null}
        {emailSend === false ? (
          <ForgetPassword
            onSubmit={data => this.onSubmit(data)}
            {...this.props}
          />
        ) : (
          <ForgetPasswordSuccess />
        )}
      </React.Fragment>
    );
  }
}

ForgetPasswordContainer.propTypes = propTypes;

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      forgotPassword: authOperations.forgotPassword
    },
    dispatch
  );

export default connect(
  null,
  mapDispatchToProps
)(ForgetPasswordContainer);
