import React from 'react';
import PropTypes from 'prop-types';
import { Col, Form } from 'react-bootstrap';
import Multiselect from 'react-widgets/lib/Multiselect';

const propTypes = {
  label: PropTypes.string.isRequired,
  horizontal: PropTypes.bool.isRequired,
  icon: PropTypes.symbol.isRequired,
  labelSize: PropTypes.string.isRequired,
  input: PropTypes.string.isRequired,
  meta: PropTypes.shape({
    error: PropTypes.string,
    warning: PropTypes.string,
    touched: PropTypes.bool
  }).isRequired
};

const FieldMultiSelect = ({
  horizontal,
  icon,
  input,
  label,
  labelSize,
  meta: { error, warning, touched },
  ...props
}) => {
  const labelProps = horizontal ? { column: true, ...labelSize } : {};
  const formLabel = label ? (
    <Form.Label {...labelProps}>
      {icon && <i className={`mr-1 icon-${icon}`} />}
      {label}
    </Form.Label>
  ) : (
    ''
  );

  const control = (
    <React.Fragment>
      <Multiselect
        isInvalid={touched && (!!error || !!warning)}
        {...input}
        onBlur={() => input.onBlur()}
        value={input.value || []} // requires value to be an array
        {...props}
      />
      {touched && (error || warning) && (
        <Form.Control.Feedback type="invalid">
          {error || warning}
        </Form.Control.Feedback>
      )}
    </React.Fragment>
  );

  return horizontal ? (
    <Form.Group as={Form.Row}>
      {formLabel}
      <Col>{control}</Col>
    </Form.Group>
  ) : (
    <Form.Group>
      {formLabel}
      {control}
    </Form.Group>
  );
};

FieldMultiSelect.propTypes = propTypes;

export default FieldMultiSelect;
