import React from 'react';

function CustomRadio({
  className,
  id,
  inline,
  input,
  isIndeterminate,
  label,
  labelProps,
  meta: { error, warning, touched } = {},
  options,
  ...props
}) {
  const setCheckboxRef = radio => {
    if (radio) {
      radio.indeterminate = isIndeterminate;
    }
  };

  const addClasses = [];

  const isInvalid = !!(touched && (error || warning));

  if (isInvalid) {
    addClasses.push('is-invalid');
  }

  if (inline) {
    addClasses.push('custom-control-inline align-middle');
  }

  const radio = (
    <div>
      {options.map(o => (
        <div
          key={o.value}
          className={`custom-control custom-${
            props.type
          } mr-0 ${addClasses.join(' ')} ${className}`}
        >
          <input
            className="custom-control-input"
            id={o.id}
            value={o.value}
            ref={setCheckboxRef}
            {...input}
            {...props}
          />

          <label
            className="custom-control-label mr-5"
            htmlFor={o.id}
            {...labelProps}
          >
            {o.title}
          </label>
        </div>
      ))}

      {touched && (error || warning) && <div>{error || warning}</div>}
    </div>
  );

  return isInvalid ? radio : radio;
}

export default CustomRadio;
