import React from 'react';
import { Field, reduxForm } from 'redux-form';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { Button, Form, Col, Row } from 'react-bootstrap';
import FieldInput from '../Shared/FieldInput';
import Validators from '../../utils/validators';

const propTypes = {
  handleSubmit: PropTypes.func.isRequired
};

const validatePassword = (value, allValues) =>
  value && value !== allValues.newPassword
    ? 'Password and Confirm Password should match'
    : undefined;

function ChangePassword({ handleSubmit }) {
  return (
    <div className="formWrapper commonForAll">
      <div className="formBody">
        <h5>
          <b>Change Password</b>
        </h5>
        <p style={{ marginBottom: 25 }}>
          To change your password, enter the old password you use to sign in,
          and enter new password as you desire
        </p>
        <Form autoComplete="off" onSubmit={handleSubmit}>
          <Row>
            <Col>
              <Field
                name="oldPassword"
                horizontal
                maxLength={50}
                type="password"
                component={FieldInput}
                placeholder="Enter Old Password"
                validate={[Validators.required, Validators.password]}
              />
            </Col>
          </Row>
          <Row>
            <Col>
              <Field
                component={FieldInput}
                horizontal
                maxLength={50}
                name="newPassword"
                placeholder="Password"
                type="password"
                validate={[Validators.required, Validators.password]}
              />
            </Col>
          </Row>
          <Row>
            <Col>
              <Field
                component={FieldInput}
                horizontal
                maxLength={50}
                name="confirmNewPassword"
                placeholder="ConfirmPassword"
                type="password"
                validate={[
                  Validators.required,
                  Validators.password,
                  validatePassword
                ]}
              />
            </Col>
          </Row>
          <div className="form-group">
            <Button variant="primary" type="submit">
              Change Password
            </Button>
            &nbsp;
            <Link to="/">Cancel</Link>
          </div>
        </Form>
      </div>
    </div>
  );
}

ChangePassword.propTypes = propTypes;

export default reduxForm({
  form: 'Change_Password'
})(ChangePassword);
