import actions from './snapBuilderActions';
import snapBuilderService from '../../api/snapBuilderService';

const getAdAccountList = () => dispatch => {
  snapBuilderService.getAdAccountList().then(({ data }) => {
    dispatch(actions.setAdAccountList(data.data));
  });
};

const uploadMedia = (formData, callbackSuccess, callbackError) => dispatch => {
  snapBuilderService.uploadMedia(formData).then(
    ({ data }) => {
      dispatch(actions.setUploadMedia(data));
      callbackSuccess(data);
    },
    error => {
      callbackError(error.data.Message);
    }
  );
};

export default {
  getAdAccountList,
  uploadMedia
};
