import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { Alert } from 'react-bootstrap';
import { authOperations } from '../../modules/auth';
import ResetPassword from './ResetPassword';
import ResetPasswordSuccess from './ResetPasswordSuccess';

const propTypes = {
  verifyToken: PropTypes.func.isRequired,
  match: PropTypes.shape({
    params: PropTypes.object
  }).isRequired,
  resetPassword: PropTypes.func.isRequired
};

class ResetPasswordContainer extends Component {
  state = {
    errMsg: '',
    resetSuccess: false,
    tokenVerified: false
  };

  componentDidMount() {
    const { match, verifyToken } = this.props;

    verifyToken(
      match.params.key,
      () => {
        this.setState({
          tokenVerified: true
        });
      },
      error => {
        this.setState({
          errMsg: error.data.message
        });
      }
    );
  }

  onSubmit(data) {
    const { resetPassword, match } = this.props;

    data.token = match.params.key;

    resetPassword(
      data,
      () => {
        this.setState({
          errMsg: '',
          resetSuccess: true
        });
      },
      error => {
        this.setState({
          errMsg: error.data.message
        });
      }
    );
  }

  renderBody() {
    const { resetSuccess, tokenVerified } = this.state;
    if (resetSuccess) {
      return <ResetPasswordSuccess />;
    }

    if (tokenVerified) {
      return (
        <ResetPassword onSubmit={data => this.onSubmit(data)} {...this.props} />
      );
    }

    return null;
  }

  render() {
    const { errMsg } = this.state;

    return (
      <>
        {errMsg && (
          <Alert variant="danger" className="notPasswordScreen">
            {errMsg}
          </Alert>
        )}

        {this.renderBody()}
      </>
    );
  }
}

ResetPasswordContainer.propTypes = propTypes;

const mapStateToProps = state => ({
  token: state.auth.verifyToken
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      verifyToken: authOperations.verifyToken,
      resetPassword: authOperations.resetPassword
    },
    dispatch
  );

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(ResetPasswordContainer)
);
