import React from 'react';
import logoFluent from '../../../assets/logoFluent.png';
import './CalebLogo.scss';

function CalebLogo() {
  return (
    <div className="caleb-logo d-flex align-items-center">
      <div className="caleb-logo__img-container">
        <img alt="Fluent" src={logoFluent} />
      </div>

      <div className="caleb-logo__title">CALEB</div>
    </div>
  );
}

export default CalebLogo;
