import React from 'react';
import { Row, Col } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
import { ROUTES_SNAP_BUILDER_UPLOAD_CREATIVES } from '../../utils';

function SnapBuilder() {
  return (
    <React.Fragment>
      <Row className="pl-3">
        <h3>All Creatives</h3>
      </Row>
      <Row className="d-flex justify-content-between">
        <Col>756 Total Creatives</Col>
        <Col>
          <NavLink
            className="btn btn-secondary mr-2 float-right shadow rounded"
            exact
            to={ROUTES_SNAP_BUILDER_UPLOAD_CREATIVES}
          >
            UPLOAD CREATIVES
          </NavLink>
        </Col>
      </Row>
      <hr />
    </React.Fragment>
  );
}

export default SnapBuilder;
