import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { Link, Redirect } from 'react-router-dom';
import { ROUTE_LOGIN, ROUTE_SECURE_ROUTES } from '../../../utils';
import CalebLogo from '../CalebLogo';
import AuthHero from './AuthHero';
import showBackLink from './showBackLink';
import './AuthLayout.scss';

const propTypes = {
  component: PropTypes.elementType.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  route: PropTypes.shape({
    route: PropTypes.object,
    match: PropTypes.any
  }).isRequired
};

class AuthLayout extends PureComponent {
  render() {
    const { component: Component, isLoggedIn, route } = this.props;

    return isLoggedIn ? (
      <Redirect to={ROUTE_SECURE_ROUTES} />
    ) : (
      <div id="auth-layout" className="auth-layout">
        <div className="auth-layout__form-container">
          <div className="auth-layout__back-link">
            {showBackLink(route.match) && (
              <Link to={ROUTE_LOGIN}>{'< Back to Sign In page'}</Link>
            )}
          </div>

          <CalebLogo />

          <Component {...route} />
        </div>

        <div className="auth-layout__hero-container">
          <AuthHero />
        </div>
      </div>
    );
  }
}

AuthLayout.propTypes = propTypes;

export default AuthLayout;
