function cancellablePromise(promise) {
  let hasCancelled = false;

  const wrappedPromise = new Promise((resolve, reject) => {
    promise.then(
      val =>
        hasCancelled ? reject(new Error({ isCancelled: true })) : resolve(val),
      error =>
        hasCancelled ? reject(new Error({ isCancelled: true })) : reject(error)
    );
  });

  return {
    promise: wrappedPromise,
    cancel() {
      hasCancelled = true;
    }
  };
}

export default cancellablePromise;
