import React from 'react';
import PropTypes from 'prop-types';
import { NavLink, Link } from 'react-router-dom';
import { Button, Container, Dropdown, Nav } from 'react-bootstrap';
import {
  ROUTE_SNAP_BUILDER,
  ROUTE_BATCH_PROCESSING,
  ROUTE_DOWNLOAD_REPORTS,
  ROUTE_CHANGEPASSWORD
} from '../../../../utils';
import './Header.scss';
import logoFluent from '../../../../assets/logoFluent.png';
import defaultAvatar from '../../../../assets/defaultAvatar.svg';

const propTypes = {
  logout: PropTypes.func.isRequired,
  userData: PropTypes.shape({
    avatar: PropTypes.object,
    name: PropTypes.string
  }).isRequired
};

function Header(props) {
  const { logout, userData } = props;

  return (
    <header className="header">
      <Container>
        <div className="d-flex align-items-center">
          <div className="header__logo d-flex align-items-center">
            <div className="mr-2">
              <img alt="Fluent" src={logoFluent} />
            </div>
            <div className="mr-2">|</div>
            <div>CALEB</div>
          </div>

          <Nav className="header__tabs" variant="tabs">
            <Nav.Item>
              <NavLink to={ROUTE_SNAP_BUILDER}>
                <i className="icon-snapchat-ghost" />
                Snap Builder
              </NavLink>
            </Nav.Item>

            <Nav.Item>
              <NavLink to={ROUTE_BATCH_PROCESSING}>
                <i className="icon-upload" />
                Batch Processing
              </NavLink>
            </Nav.Item>

            <Nav.Item>
              <NavLink to={ROUTE_DOWNLOAD_REPORTS}>
                <i className="icon-download" />
                Download Report
              </NavLink>
            </Nav.Item>
          </Nav>

          <div className="ml-auto">
            <Dropdown alignRight>
              <Dropdown.Toggle
                className="user-dropdown__button"
                id="user-dropdown"
                variant="link"
              >
                <div className="user-dropdown__img-wrapper">
                  <img src={userData.avatar || defaultAvatar} alt="" />
                </div>
                {userData.name}
              </Dropdown.Toggle>

              <Dropdown.Menu>
                <Dropdown.Item as={Link} to={ROUTE_CHANGEPASSWORD}>
                  Change Password
                </Dropdown.Item>
                <Dropdown.Item as={Button} onClick={logout}>
                  Logout
                </Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          </div>
        </div>
      </Container>
    </header>
  );
}

Header.propTypes = propTypes;

export default Header;
